import uniIcons from '../uni-icons/uni-icons.vue'
export default {
	name: 'uniCombox',
	components: {
		uniIcons
	},
	props: {
		label: {
			type: String,
			default: ''
		},
		labelWidth: {
			type: String,
			default: 'auto'
		},
		placeholder: {
			type: String,
			default: ''
		},
		candidates: {
			type: Array,
			default () {
				return []
			}
		},
		emptyTips: {
			type: String,
			default: '无匹配项'
		},
		value: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			showSelector: false,
			inputVal: ''
		}
	},
	computed: {
		labelStyle() {
			if (this.labelWidth === 'auto') {
				return {}
			}
			return {
				width: this.labelWidth
			}
		},
		filterCandidates() {
			return this.candidates.filter((item) => {
				return item.indexOf(this.inputVal) > -1
			})
		},
		filterCandidatesLength() {
			return this.filterCandidates.length
		}
	},
	watch: {
		value: {
			handler(newVal) {
				this.inputVal = newVal
			},
			immediate: true
		}
	},
	methods: {
		toggleSelector() {
			this.showSelector = !this.showSelector
		},
		onFocus() {
			this.showSelector = true
		},
		onBlur() {
			setTimeout(() => {
				this.showSelector = false
			}, 50)
		},
		onSelectorClick(index) {
			this.inputVal = this.filterCandidates[index]
			this.showSelector = false
			this.$emit('input', this.inputVal)
		},
		onInput() {
			setTimeout(() => {
				this.$emit('input', this.inputVal)
			})
		}
	}
}
